import { css, styled } from '../../utils/styled';
import { color, fontFamily, radius, spacing, typography, variants, zIndex } from '../../utils/theme';
import type { ModalProps } from './Modal';

export type ModalSize = 'default' | 'wide';

const spacingOnLargeScreens = 7;
const spacingOnSmallScreens = 5;
const doublePadding = 24;
const smallWidth = 320;
const wideWidth = 840;
const defaultWidth = 640;

type DialogProps = {
	size?: ModalSize;
	overflow?: ModalProps['overflow'];
};

export const Header = styled.header`
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	padding: ${spacing(spacingOnSmallScreens)};

	@media (min-width: 768px) {
		padding: ${spacing(spacingOnLargeScreens)};
	}
`;

export const Title = styled.h2`
	font-family: ${fontFamily('default')};
	${typography('heading4')}
	color: ${color('content.primary')};
	width: 100%;
	margin: 0;
`;

export const Section = styled.div`
	padding: ${spacing(0, spacingOnSmallScreens, spacingOnSmallScreens, spacingOnSmallScreens)};

	@media (min-width: 768px) {
		padding: ${spacing(0, spacingOnLargeScreens, spacingOnLargeScreens, spacingOnLargeScreens)};
	}
`;

export const Actions = styled.div`
	padding-top: ${spacing(6)};

	@media (min-width: 768px) {
		padding-top: ${spacing(7)};
	}
`;

export const DialogOverlay = styled.div`
	background: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	overflow: auto;
	z-index: ${zIndex('modal')};
`;

export const StyledDialog = styled.dialog<DialogProps>`
	background-color: ${color('background.primary')};
	border-radius: ${radius('md')};
	border: none;
	box-sizing: border-box;
	font-family: ${fontFamily('default')};
	${typography('description')}
	color: ${color('content.primary')};
	margin: revert; // ensure that browser can determine the correct margins to position the dialog
	margin-top: ${spacing(3)};
	padding: 0;
	overflow: ${({ overflow }) => overflow};

	@media (min-width: 768px) {
		margin-top: ${spacing(7)};
	}

	width: 100%;

	${variants(
		'size',
		{
			small: css`
				max-width: calc(100% - ${doublePadding}px);
				@media (min-width: ${smallWidth + doublePadding}px) {
					max-width: ${smallWidth}px;
				}
			`,
			wide: css`
				max-width: calc(100% - ${doublePadding}px);
				@media (min-width: ${wideWidth + doublePadding}px) {
					max-width: ${wideWidth}px;
				}
			`,
			default: css`
				max-width: calc(100% - ${doublePadding}px);
				@media (min-width: ${defaultWidth + doublePadding}px) {
					max-width: ${defaultWidth}px;
				}
			`,
		},
		'default',
	)}
`;
